let code = {};

code.base = `
<template>
    <i-button type="primary" @click="modal1 = true"></i-button>
    <modal
        value="{{modal1}}"
        width="{{360}}"
        title="Modal"
        on-ok="ok"
        on-close="closeModal1">
        <p></p>
        <p></p>
        <p></p>
    </modal>
</template>
<script>
    import {Button, Modal, Message} from 'san-iview';
    export default {
        components: {
            'modal': Modal
            'i-button': Button
        },
        initData () {
            return {
                modal1: false
            }
        },
        ok () {
            Message.info('');
        },
        closeModal1 () {
            this.data.set('modal1', true);
            Message.info('');
        }
    }
</script>
`;

code.custom = `
<template>
    <i-button on-select="handleModal2"></i-button>
    <modal 
        s-if="modal2"
        value="{{modal2}}" 
        on-close="closeModal2"
        >
        <p slot="header" style="color:#f60;text-align:center">
            <Icon type="information-circled"></Icon>
            <span></span>
        </p>
        <div style="text-align:center">
            <p> 10 </p>
            <p></p>
        </div>
        <div slot="footer">
            <i-button type="error" size="large" long loading="{=modal_loading=}" on-select="del"></i-button>
        </div>
    </modal>
    <i-button on-select="handleModal3"></i-button>
    <modal 
        s-if="modal3"
        value="{=modal3=}"
        showHead="{{false}}"
        on-close="closeModal3"
        >
        <p></p>
        <p></p>
        <p></p>
    </modal>
    <i-button on-select="handleModal5"></i-button>
    <modal
        s-if="modal4"
        value="{{modal4}}"
        title=""
        width="{{300}}"
        on-close="closeModal5">
        <p> px 520px</p>
        <p> 768px <code>auto</code></p>
    </modal>
</template>
<script>
    import {Button, Modal, Message} from 'san-iview';
    export default {
        components: {
            'modal': Modal
            'i-button': Button
        },
        initData () {
            return {
                modal2: false,
                modal_loading: false,
                modal3: false,
                modal4: false
            }
        },
        del () {
            this.modal_loading = true;
            setTimeout(() => {
                this.modal_loading = false;
                this.modal2 = false;
                this.$Message.success('');
            }, 2000);
        },
        handleModal2() {
            this.data.set('modal2', true);
        },
        closeModal2() {
            this.data.set('modal2', false);
            Message.info('');
        },
        handleModal3() {
            this.data.set('modal3', true);
        },
        closeModal3() {
            this.data.set('modal3', false);
            Message.info('');
        },
        handleModal4() {
            this.data.set('modal4', true);
        },
        closeModal4() {
            this.data.set('modal4', false);
            Message.info('');
        },
    }
</script>
`;

code.async = `
<template>
    <i-button type="primary" @click="modal6 = true"></i-button>
    <modal
        v-model="modal6"
        title=""
        :loading="loading"
        @on-ok="asyncOK">
        <p> 2 </p>
    </modal>
</template>
<script>
    export default {
        data () {
            return {
                modal6: false,
                loading: true
            }
        },
        methods: {
            asyncOK () {
                setTimeout(() => {
                    this.modal6 = false;
                }, 2000);
            }
        }
    }
</script>
`;

