code.baseInstance = `
<template>
    <i-button @click="instance('info')"></i-button>
    <i-button @click="instance('success')"></i-button>
    <i-button @click="instance('warning')"></i-button>
    <i-button @click="instance('error')"></i-button>
</template>
<script>
    export default {
        methods: {
            instance (type) {
                const title = '';
                const content = '<p></p><p></p>';
                switch (type) {
                    case 'info':
                        this.$modal.info({
                            title: title,
                            content: content
                        });
                        break;
                    case 'success':
                        this.$modal.success({
                            title: title,
                            content: content
                        });
                        break;
                    case 'warning':
                        this.$modal.warning({
                            title: title,
                            content: content
                        });
                        break;
                    case 'error':
                        this.$modal.error({
                            title: title,
                            content: content
                        });
                        break;
                }
            }
        }
    }
</script>
`;

code.confirm = `
<template>
    <i-button @click="confirm"></i-button>
    <i-button @click="custom"></i-button>
    <i-button @click="async"></i-button>
</template>
<script>
    export default {
        methods: {
            confirm () {
                this.$modal.confirm({
                    title: '',
                    content: '<p></p><p></p>',
                    onOk: () => {
                        this.$Message.info('');
                    },
                    onCancel: () => {
                        this.$Message.info('');
                    }
                });
            },
            custom () {
                this.$modal.confirm({
                    title: '',
                    content: '<p></p><p></p>',
                    okText: 'OK',
                    cancelText: 'Cancel'
                });
            },
            async () {
                this.$modal.confirm({
                    title: '',
                    content: '<p> 2 </p>',
                    loading: true,
                    onOk: () => {
                        setTimeout(() => {
                            this.$modal.remove();
                            this.$Message.info('');
                        }, 2000);
                    }
                });
            }
        }
    }
</script>
`;

code.render = `
<template>
    <p>
        {{ value }}
    </p>
    <p>
        <i-button @click="render"></i-button>
    </p>
</template>
<script>
    export default {
        data () {
            return {
                value: ''
            }            
        },
        methods: {
            render () {
                this.$modal.confirm({
                    render: (h) => {
                        return h('Input', {
                            props: {
                                value: this.value,
                                autofocus: true,
                                placeholder: 'Please enter your name...'
                            },
                            on: {
                                input: (val) => {
                                    this.value = val;
                                }
                            }
                        })
                    }
                })
            }
        }
    }
</script>
`;

export default code;


