let code = {};

code.base = `
<template>
<div slot="demo">
    <Tabs name="name1">
        <TabPane label="" name="name1"></TabPane>
        <TabPane label="" name="name2"></TabPane>
        <TabPane label="" name="name3"></TabPane>
    </Tabs>
</div>
</template>
<script>
    import {Tabs} from 'san-iview';

    export default {
        components: {
            'tabs': Tabs,
            'tabpane': Tabs.Pane
        }
    }
</script>
`;

code.disabled = `
<template>
    <Tabs>
        <TabPane label=""></TabPane>
        <TabPane label="" disabled></TabPane>
        <TabPane label=""></TabPane>
    </Tabs>
</template>
<script>
    import {Tabs} from 'san-iview';

    export default {
        components: {
            'tabs': Tabs,
            'tabpane': Tabs.Pane
        }
    }
</script>
`;

code.icon = `
<template>
    <Tabs>
        <TabPane label="macOS" icon="social-apple"></TabPane>
        <TabPane label="Windows" icon="social-windows"></TabPane>
        <TabPane label="Linux" icon="social-tux"></TabPane>
    </Tabs>
</template>
<script>
    import {Tabs} from 'san-iview';

    export default {
        components: {
            'tabs': Tabs,
            'tabpane': Tabs.Pane
        }
    }
</script>
`;

code.mini = `
<template>
    <Tabs size="small">
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
    </Tabs>
</template>
<script>
    import {Tabs} from 'san-iview';

    export default {
        components: {
            'tabs': Tabs,
            'tabpane': Tabs.Pane
        }
    }
</script>
`;

code.card = `
<template>
    <Tabs type="card">
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
    </Tabs>
</template>
<script>
    import {Tabs} from 'san-iview';

    export default {
        components: {
            'tabs': Tabs,
            'tabpane': Tabs.Pane
        }
    }
</script>
`;

code.closable = `
<template>
    <Tabs type="card" closable @on-tab-remove="handleTabRemove">
        <TabPane label="" v-if="tab0"></TabPane>
        <TabPane label="" v-if="tab1"></TabPane>
        <TabPane label="" v-if="tab2"></TabPane>
    </Tabs>
</template>
<script>
    import {Tabs} from 'san-iview';

    export default {
        components: {
            'tabs': Tabs,
            'tabpane': Tabs.Pane
        },
        initData () {
            return {
                tab0: true,
                tab1: true,
                tab2: true
            }
        },
        methods: {
            handleTabRemove (name) {
                this['tab' + name] = false;
            }
        }
    }
</script>
`;

code.render = `
<template>
    <Tabs value="name1">
        <TabPane :label="label" name="name1"></TabPane>
        <TabPane label="" name="name2"></TabPane>
        <TabPane label="" name="name3"></TabPane>
    </Tabs>
</template>
<script>
    export default {
        data () {
            return {
                label: (h) => {
                    return h('div', [
                        h('span', ''),
                        h('Badge', {
                            props: {
                                count: 3
                            }
                        })
                    ])
                }
            }
        }
    }
</script>
`;

code.extra = `
<template>
    <Tabs type="card">
        <TabPane v-for="tab in tabs" :key="tab" :label="'' + tab">{{ tab }}</TabPane>
        <Button type="ghost" @click="handleTabsAdd" size="small" slot="extra" :disabled="tabs > 3"></Button>
    </Tabs>
</template>
<script>
    import {Tabs} from 'san-iview';

    export default {
        components: {
            'tabs': Tabs,
            'tabpane': Tabs.Pane
        },
        initData () {
            return {
                tabs: 2
            }
        },
        handleTabsAdd () {
            this.data.push('tabs', 1);
        }
    }
</script>
`;

code.animated = `
<template>
    <Tabs :animated="false">
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
    </Tabs>
</template>
<script>
    export default {
        
    }
</script>
`;

code.style = `
<style>
    .demo-tabs-style1 > .ivu-tabs-card > .ivu-tabs-content {
        height: 120px;
        margin-top: -16px;
    }

    .demo-tabs-style1 > .ivu-tabs-card > .ivu-tabs-content > .ivu-tabs-tabpane {
        background: #fff;
        padding: 16px;
    }

    .demo-tabs-style1 > .ivu-tabs.ivu-tabs-card > .ivu-tabs-bar .ivu-tabs-tab {
        border-color: transparent;
    }

    .demo-tabs-style1 > .ivu-tabs-card > .ivu-tabs-bar .ivu-tabs-tab-active {
        border-color: #fff;
    }
    .demo-tabs-style2 > .ivu-tabs.ivu-tabs-card > .ivu-tabs-bar .ivu-tabs-tab{
        border-radius: 0;
        background: #fff;
    }
    .demo-tabs-style2 > .ivu-tabs.ivu-tabs-card > .ivu-tabs-bar .ivu-tabs-tab-active{
        border-top: 1px solid #3399ff;
    }
    .demo-tabs-style2 > .ivu-tabs.ivu-tabs-card > .ivu-tabs-bar .ivu-tabs-tab-active:before{
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background: #3399ff;
        position: absolute;
        top: 0;
        left: 0;
    }
</style>
<template>
    <Row :gutter="32">
        <Col span="12" class="demo-tabs-style1" style="background: #e3e8ee;padding:16px;">
            <Tabs type="card">
                <TabPane label=""></TabPane>
                <TabPane label=""></TabPane>
                <TabPane label=""></TabPane>
            </Tabs>
        </Col>
        <Col span="12" class="demo-tabs-style2">
            <Tabs type="card">
                <TabPane label=""></TabPane>
                <TabPane label=""></TabPane>
                <TabPane label=""></TabPane>
            </Tabs>
        </Col>
    </Row>
</template>
<script>
    import {Tabs} from 'san-iview';

    export default {
        components: {
            'tabs': Tabs,
            'tabpane': Tabs.Pane
        }
    }
</script>
`;

export default code;


