let code = {};

code.info = `
<template>
    <i-button type="primary" on-select="info"></i-button>
</template>
<script>
    export default {
        methods: {
            info () {
                Message.info('');
            }
        }
    }
</script>
`;

code.type = `
<template>
    <i-button on-select="success"></i-button>
    <i-button on-select="warning"></i-button>
    <i-button on-select="error"></i-button>
</template>
<script>
    export default {
        methods: {
            success () {
                Message.success('');
            },
            warning () {
                Message.warning('');
            },
            error () {
                Message.error('');
            }
        }
    }
</script>
`;

code.loading = `
<template>
    <i-button on-select="loading">...</i-button>
</template>
<script>
    export default {
        methods: {
            loading () {
                const msg = Message.loading({
                    content: '...',
                    duration: 0
                });
                setTimeout(msg, 3000);
            },
        }
    }
</script>
`;

code.time = `
<template>
    <i-button on-select="time">10</i-button>
</template>
<script>
    export default {
        methods: {
            Message.info({
                content: '10',
                duration: 10
            });
        }
    }
</script>
`;

code.config = `
Message.config({
    top: 50,
    duration: 3
});
`;

code.closable = `
<template>
    <i-button on-select="closable"></i-button>
</template>
<script>
    export default {
        methods: {
            closable () {
                Message.info({
                    content: '',
                    duration: 10,
                    closable: true,
                    onClose: () => {
                        console.log('close');
                    }
                });
            }
        }
    }
</script>
`;

export default code;


