let code = {};

code.base = `
<template>
<div>
    <Collapse name="{=value1=}">
        <Panel name="1">
            <span slot="header"></span>
            <p slot="content">Steve Jobs1955224</p>
        </Panel>
        <Panel name="2">
            <span slot="header"></span>
            <p slot="content">Stephen Gary WozniakUC BerkeleyEECS1987</p>
        </Panel>
        <Panel name="3">
            <span slot="header"></span>
            <p slot="content">AppleiPodiMaciPhoneiPad</p>
        </Panel>
    </Collapse>
</div>
</template>
<script>
    import {Collapse} from 'san-iview';

    export default {
        components: {
            'collapse': Collapse,
            'panel': Collapse.Panel
        },
        initData () {
            return {
                value1: '1'
            }
        }
    }
</script>
`;

code.accordion = `
<template>
<div>
    <Collapse accordion name="{=value2=}">
        <Panel name="1">
            <span slot="header"></span>
            <p slot="content">Steve Jobs1955224</p>
        </Panel>
        <Panel name="2">
            <span slot="header"></span>
            <p slot="content">Stephen Gary WozniakUC BerkeleyEECS1987</p>
        </Panel>
        <Panel name="3">
            <span slot="header"></span>
            <p slot="content">AppleiPodiMaciPhoneiPad</p>
        </Panel>
    </Collapse>
</div>
</template>
<script>
    import {Collapse} from 'san-iview';

    export default {
        components: {
            'collapse': Collapse,
            'panel': Collapse.Panel
        },
        initData () {
            return {
                value2: '1'
            }
        }
    }
</script>
`;

code.nest = `
<template>
<div>
    <Collapse accordion name="{=value3=}">
        <Panel name="1">
            <span slot="header"></span>
            <div slot="content">
                Steve Jobs1955224
                <Collapse accordion name="{=value4=}">
                    <Panel name="1-1">
                        <span slot="header">iPhone</span>
                        <p slot="content">iPhoneiOSiPhone2007192007629</p>
                    </Panel>
                    <Panel name="1-2">
                        <span slot="header">iPad</span>
                        <p slot="content">iPad2010iPhone4iPhoneJonathan Ive Dieter Rams</p>
                    </Panel>
                </Collapse>
            </div>
        </Panel>
        <Panel name="2">
            <span slot="header"></span>
            <p slot="content">Stephen Gary WozniakUC BerkeleyEECS1987</p>
        </Panel>
        <Panel name="3">
            <span slot="header"></span>
            <p slot="content">AppleiPodiMaciPhoneiPad</p>
        </Panel>
    </Collapse>
</div>
</template>
<script>
    import {Collapse} from 'san-iview';

    export default {
        components: {
            'collapse': Collapse,
            'panel': Collapse.Panel
        },
        initData () {
                value3: '1',
                value4: '1-1'
            }
        }
    }
</script>
`;

export default code;


