let code = {};

code.open = `
<template>
    <i-btn type="primary" on-select="open(false)"></i-btn>
    <i-btn on-select="open(true)"></i-btn>
</template>
<script>
    import {Notice, Button} from 'san-iview';

    export default {
        components: {
            'i-btn': button
        },
        open (nodesc) {
            Notice.open({
                title: '',
                desc: nodesc ? '' : ',,,'
            });
        }
    }
</script>
`;

code.type = `
<template>
    <p></p>
    <i-btn on-select="info(false)"></i-btn>
    <i-btn on-select="success(false)"></i-btn>
    <i-btn on-select="warning(false)"></i-btn>
    <i-btn on-select="error(false)"></i-btn>
    <p></p>
    <i-btn on-select="info(true)"></i-btn>
    <i-btn on-select="success(true)"></i-btn>
    <i-btn on-select="warning(true)"></i-btn>
    <i-btn on-select="error(true)"></i-btn>
</template>
<script>
    import {Notice, Button} from 'san-iview';

    export default {
        components: {
            'i-btn': button
        },
        info (nodesc) {
            Notice.info({
                title: '',
                desc: nodesc ? '' : ',,,'
            });
        },
        success (nodesc) {
            Notice.success({
                title: '',
                desc: nodesc ? '' : ',,,'
            });
        },
        warning (nodesc) {
            Notice.warning({
                title: '',
                desc: nodesc ? '' : ',,,'
            });
        },
        error (nodesc) {
            Notice.error({
                title: '',
                desc: nodesc ? '' : ',,,'
            });
        }
    }
</script>
`;

code.time = `
<template>
    <i-btn type="primary" on-select="time"></i-btn>
</template>
<script>
    import {Notice, Button} from 'san-iview';

    export default {
        components: {
            'i-btn': button
        },
        time () {
            Notice.open({
                title: '',
                desc: '',
                duration: 0
            });
        }
    }
</script>
`;

code.config = `
Notice.config({
    top: 50,
    duration: 3
});
`;

export default code;


