let code = {};

code.single = `
<template>
    <radio v-model="single">radio</radio>
</template>
<script>
    export default {
        data () {
            return {
                single: false
            }
        }
    }
</script>
`;

code.group = `
<template>
    <radio-group label="{=phone=}">
        <radio label="apple">
            <Icon type="social-apple"></Icon>
            <span>Apple</span>
        </radio>
        <radio label="android">
            <Icon type="social-android"></Icon>
            <span>Android</span>
        </radio>
        <radio label="windows">
            <Icon type="social-windows"></Icon>
            <span>Windows</span>
        </radio>
    </radio-group>
    <radio-group label="{=animal=}">
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
    </radio-group>
</template>
<script>
    import {Radio, Icon} from 'san-iview';

    export default {
        components: {
            'radio': Radio,
            'radio-group': Radio.Group
        },
        initData () {
            return {
                phone: 'apple',
                animal: ''
            }
        }
    };
</script>
`;

code.disabled = `
<template>
    <radio-group label="{=disabledGroup=}">
        <radio label="" disabled></radio>
        <radio label=""></radio>
        <radio label=""></radio>
    </radio-group>
</template>
<script>
    import {Radio} from 'san-iview';

    export default {
        components: {
            'radio': Radio,
            'radio-group': Radio.Group
        },
        initData () {
            return {
                disabledGroup: ''
            }
        }
    }
</script>
`;

code.vertical = `
<template>
    <radio-group label="{=vertical=}" vertical>
        <radio label="apple">
            <Icon type="social-apple"></Icon>
            <span>Apple</span>
        </radio>
        <radio label="android">
            <Icon type="social-android"></Icon>
            <span>Android</span>
        </radio>
        <radio label="windows">
            <Icon type="social-windows"></Icon>
            <span>Windows</span>
        </radio>
    </radio-group>
</template>
<script>
    import {Radio} from 'san-iview';

    export default {
        components: {
            'radio': Radio,
            'radio-group': Radio.Group
        },
        initData () {
            return {
                vertical: 'apple'
            }
        }
    }
</script>
`;

code.button = `
<template>
    <radio-group label="{=button1=}" type="button">
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
    </radio-group>
    <radio-group label="button2" type="button">
        <radio label=""></radio>
        <radio label="" disabled></radio>
        <radio label=""></radio>
        <radio label=""></radio>
    </radio-group>
    <radio-group label="{=button3=}" type="button">
        <radio label="" disabled></radio>
        <radio label="" disabled></radio>
        <radio label="" disabled></radio>
        <radio label="" disabled></radio>
    </radio-group>
</template>
<script>
    import {Radio} from 'san-iview';

    export default {
        components: {
            'radio': Radio,
            'radio-group': Radio.Group
        },
        initData () {
            return {
                button1: '',
                button2: '',
                button3: ''
            }
        }
    }
</script>
`;

code.size = `
<template>
    <radio-group label="{=button4=}" type="button" size="large">
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
    </radio-group>
    <radio-group label="{=button5=}" type="button">
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
    </radio-group>
    <radio-group label="{=button6=}" type="button" size="small">
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
        <radio label=""></radio>
    </radio-group>
</template>
<script>
    import {Radio} from 'san-iview';

    export default {
        components: {
            'radio': Radio,
            'radio-group': Radio.Group
        },
        initData () {
            return {
                button4: '',
                button5: '',
                button6: ''
            }
        }
    }
</script>
`;

export default code;


