let code = {};

code.base = `
<template>
<div>
    <menu mode="horizontal" theme="{{theme1}}" activeName="1">
        <menu-item name="1">
            <Icon type="ios-paper"></Icon>
            
        </menu-item>
        <menu-item name="2">
            <Icon type="ios-people"></Icon>
            
        </menu-item>
        <submenu name="3">
            <span slot="title">
                <Icon type="stats-bars"></Icon>
                
            </span>
            <menu-group title="">
                <menu-item name="3-1"></menu-item>
                <menu-item name="3-2"></menu-item>
                <menu-item name="3-3"></menu-item>
            </menu-group>
            <menu-group title="">
                <menu-item name="3-4"></menu-item>
                <menu-item name="3-5"></menu-item>
            </menu-group>
        </submenu>
    </menu>
    <br>
    <p></p>
    <RadioGroup v-model="theme1">
        <Radio label="light"></Radio>
        <Radio label="dark"></Radio>
        <Radio label="primary"></Radio>
    </RadioGroup>
</div>
</template>
<script>
    import {Icon, Menu, Radio} from 'san-iview';
    export default {
        components: {
            'menu': Menu,
            'menu-item': Menu.Item,
            'icon': Icon,
            'radio': Radio,
            'radio-group': Radio.Group
        },
        initData () {
            return {
                theme1: 'light'
            }
        }
    }
</script>
`;

code.vertical = `
<template>
<div>
    <menu mode="vertical" theme="{{theme2}}">
        <menu-group title="">
            <menu-item name="1-1"></menu-item>
            <menu-item name="1-2"></menu-item>
            <menu-item name="1-3"></menu-item>
        </menu-group>
        <submenu name="2">
            <span slot="title">
                <Icon type="ios-people"></Icon>
                
            </span>
            <menu-item name="2-1"></menu-item>
            <menu-item name="2-2"></menu-item>
        </submenu>
        <submenu name="3">
            <span slot="title">
                <Icon type="stats-bars"></Icon>
                
            </span>
            <menu-group title="">
                <menu-item name="3-1"></menu-item>
                <menu-item name="3-2"></menu-item>
                <menu-item name="3-3"></menu-item>
            </menu-group>
            <menu-group title="">
                <menu-item name="3-4"></menu-item>
                <menu-item name="3-5"></menu-item>
            </menu-group>
        </submenu>
    </menu>
</div>
</template>
<script>
import {Icon, Menu, Radio} from 'san-iview';
export default {
    components: {
        'menu': Menu,
        'menu-item': Menu.Item,
        'menu-group': Menu.Group,
        'icon': Icon,
        'radio': Radio,
        'radio-group': Radio.Group
    },
    initData () {
        return {
            theme1: 'light'
        }
    }
}
</script>
`;

code.group = `
<template>
    <Menu :theme="theme3" active-name="1">
        <MenuGroup title="">
            <MenuItem name="1">
                <Icon type="document-text"></Icon>
                
            </MenuItem>
            <MenuItem name="2">
                <Icon type="chatbubbles"></Icon>
                
            </MenuItem>
        </MenuGroup>
        <MenuGroup title="">
            <MenuItem name="3">
                <Icon type="heart"></Icon>
                
            </MenuItem>
            <MenuItem name="4">
                <Icon type="heart-broken"></Icon>
                
            </MenuItem>
        </MenuGroup>
    </Menu>
    <br>
    <p></p>
    <RadioGroup v-model="theme3">
        <Radio label="light"></Radio>
        <Radio label="dark"></Radio>
    </RadioGroup>
</template>
<script>
    export default {
        data () {
            return {
                theme3: 'light'
            }
        }
    }
</script>
`;

export default code;


