let code = {};

code.base = `
<template>
    <dropdown>
        <a href="javascript:void(0)">
            
            <Icon type="arrow-down-b"></Icon>
        </a>
        <dropdown-menu slot="list">
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item disabled></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item divided></dropdown-item>
        </dropdown-menu>
    </dropdown>
    <dropdown style="margin-left: 20px">
        <i-btn type="primary">
            
            <Icon type="arrow-down-b"></Icon>
        </i-btn>
        <dropdown-menu slot="list">
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item disabled></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item divided></dropdown-item>
        </dropdown-menu>
    </dropdown>
</template>
<script>
    import {Dropdown, Button} from 'san-iview';
    export default {
        components: {
            'i-btn': Button,
            'dropdown': Dropdown,
            'dropdown-item': Dropdown.Item,
            'dropdown-menu': Dropdown.Menu
        }
    }
</script>
`;

code.trigger = `
<template>
    <dropdown>
        <a href="javascript:void(0)">
            hover 
            <Icon type="arrow-down-b"></Icon>
        </a>
        <dropdown-menu slot="list">
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
        </dropdown-menu>
    </dropdown>
    <dropdown trigger="click" style="margin-left: 20px">
        <a href="javascript:void(0)">
            click 
            <Icon type="arrow-down-b"></Icon>
        </a>
        <dropdown-menu slot="list">
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
        </dropdown-menu>
    </dropdown>
    <dropdown trigger="custom" :visible="visible" style="margin-left: 20px">
        <a href="javascript:void(0)" @click="handleOpen">
            custom 
            <Icon type="arrow-down-b"></Icon>
        </a>
        <dropdown-menu slot="list">
            <p></p>
            <div style="text-align: right;margin:10px;">
                <i-btn type="primary" @click="handleClose"></i-btn>
            </div>
        </dropdown-menu>
    </dropdown>
</template>
<script>
    export default {
        data () {
            return {
                visible: false
            }
        },
        methods: {
            handleOpen () {
                this.visible = true;
            },
            handleClose () {
                this.visible = false;
            }
        }
    }
</script>
`;

code.placement = `
<template>
    <dropdown placement="bottom-start">
        <a href="javascript:void(0)">
            ()
            <Icon type="arrow-down-b"></Icon>
        </a>
        <dropdown-menu slot="list">
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
        </dropdown-menu>
    </dropdown>
    <dropdown style="margin-left: 20px">
        <a href="javascript:void(0)">
            ()
            <Icon type="arrow-down-b"></Icon>
        </a>
        <dropdown-menu slot="list">
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
        </dropdown-menu>
    </dropdown>
    <dropdown style="margin-left: 20px" placement="bottom-end">
        <a href="javascript:void(0)">
            ()
            <Icon type="arrow-down-b"></Icon>
        </a>
        <dropdown-menu slot="list">
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
        </dropdown-menu>
    </dropdown>
</template>
<script>
    export default {
        
    }
</script>
`;

code.nest = `
<template>
    <dropdown-menu>
        <a href="javascript:void(0)">
            
            <Icon type="arrow-down-b"></Icon>
        </a>
        <dropdown-menu slot="list">
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown-item></dropdown-item>
            <dropdown placement="right-start">
                <dropdown-item>
                    
                    <Icon type="ios-arrow-right"></Icon>
                </dropdown-item>
                <dropdown-menu slot="list">
                    <dropdown-item></dropdown-item>
                    <dropdown-item></dropdown-item>
                </dropdown-menu>
            </dropdown>
            <dropdown-item></dropdown-item>
        </dropdown-menu>
    </dropdown>
</template>
<script>
    export default {
        
    }
</script>
`;

export default code;


